html {
  height: 100%;
}

body, #root, .app-wrapper {
  font-family: 'Proxima Nova Regular';
  margin: auto 0;
  height: 100%;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f3f3f3;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

a {
  text-decoration: none !important;
}

.btn {
  font-weight: bold;
  text-align: center;
  border: 1px solid transparent;
  border-radius: 3px;
  margin: 2px;
  cursor: pointer;
}

.btn-success {
  border: 2px solid #00c056;
  color: #00c056;
}

.btn-success:hover {
  background-color: #00c056;
  color: #fff;
}

.btn-danger {
  border: 2px solid #ff3000;
  color: #ff3000;
}

.btn-danger:hover {
  background-color: #ff3000;
  color: #fff;
}

.react-datepicker__time-list {
  padding: 8px;
}

.ReactTable .-loading-filter {
  top: 64px !important;
  bottom: 42px !important;
}

.Select-input input {
  border: none !important;
}

.notification-info:hover {
  background-color: #c8d0dd !important;
}
*, :after, :before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
}

@font-face {
  font-family: 'Proxima Nova Black';
  src: url('/fonts/proximaNova/ProximaNovaBlack.otf');
}

